
body {
  background-color: #fafafa !important;
}
.swiper-container {
  height: 2.8rem;
}
.swiper-slide {
  background-color: #fff;
}
.swiper-pagination-bullet-active {
  color: #ffad37;
  background-color: #ffad37;
}
.swiper-slide img {
  width: 100%;
  height: 100%;
}



/*        定位模块：     */
.locationBox {
  height: 0.7rem;
  color: #ffad37;
  font-size: 0.28rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.icon-iconset0392 {
  font-size: 0.34rem;
  margin-right: 0.2rem
}



/*        定位模块结束      */


/*        茶楼列表模块：      */
.mainBox {
  width: 100%;
  height: auto;
}
.mainList {
  height: 2rem;
  border-top: solid 1px #c87b28;
  border-bottom: solid 1px #c87b28;
  box-shadow: 0 1px 5px #aaa;
  margin-bottom: 0.2rem;
}
.mainImgBox {
  width: 2.6rem;
  height: 1.8rem;
  margin-top: 0.08rem;
  border-radius: 0.05rem;
  overflow: hidden;
  float: left;
}
.mainImgBox img {
  width: 100%;
  height: 100%;
}
.mainDeta {
  float: left;
  font-size: 0.24rem;
  padding-left: 0.4rem;
  padding-top: 0.15rem;
}
.mainDeta h2 {
  font-size: 0.3rem;
  color: #222;
  padding-left: 0.05rem
}
.mainDeta .distanceP {
  color: #e58a08;
  font-size: 0.22rem;
  margin-top: 0.1rem;
  margin-bottom: 0.1rem;
}
.distanceD {
  width: 2.9rem;
  color: #666;
}



/*        茶楼列表结束      */



