
.footer {
  width: 100%;
  height: 1rem;
  background-color: rgba(255, 255, 255, 0.9);
  position: fixed;
  bottom: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  font-size: 0.30rem;
  color: #aaa;
  box-shadow: 0 0 5px #ccc;
}
.footer .act {
  color: #8e655c
}
.footerList {
  position: relative;
  text-align: center;
}
.cLogo {
  width: 1.3rem;
  height: 1.3rem;
  border-radius: 50%;
  position: absolute;
  bottom: -0.4rem;
  left: -0.6rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.cLogo img {
  width: 70%
}
.footerList i {
  display: block;
  font-size: 0.36rem;
}



