
body {
  background-color: #eee !important;
}
.TitBg {
  width: 100%;
  height: 2.7rem;
  background: url(../../../static/images/2.jpg) no-repeat;
  background-size: cover;
  position: relative;
}
.headPic {
  position: absolute;
  width: 1.5rem;
  height: 1.5rem;
  border-radius: 50%;
  background-color: #ccc;
  bottom: -0.75rem;
  left: 0.2rem;
  overflow: hidden;
}
.headPic img {
  width: 100%;
}
.nickName {
  color: #FFF;
  font-size: 0.36rem;
  position: absolute;
  bottom: 0.15rem;
  left: 33%;
}
.balanceBox {
  width: 100%;
  height: 1rem;
  background-color: #fff;
  padding-left: 35%;
}
.balanceList {
  float: left;
  min-width: 1.5rem;
  text-align: center;
  font-size: 0.24rem;
  color: #222;
  padding-top: 0.15rem
}
.balanceList p {
  margin-top: 0.05rem
}


/*列表模块：*/
.listBox {
  background-color: #fff;
  min-height: 1rem;
  margin-top: 0.2rem
}
.rowBox {
  width: 100%;
  height: 0.8rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding-left: 0.2rem;
  padding-right: 0.2rem;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  font-size: 0.24rem;
  color: #8e655c;
}
.rowBox span {
  font-size: 0.4rem;
}
.my-icon {
  font-size: 0.36rem;
  margin-right: 0.08rem;
}



